/* Mobile Rules */
.output-container{
    display: flex;
    flex-direction: row;
}

h2,h1{
    font-style: italic;
    font-size: 55px;
}

h1{
    color: hsl(259, 100%, 65%);
}

@media only screen and (min-width: 1020px) {
    h2,h1{
        font-style: italic;
        font-size: 80px;
    }
}